import React from 'react';
import styles from './NavigationItems.module.scss';
import NavigationItem from './NavigationItem/NavigationItem';

const navigationItems = (props) => {
    return(
    <ul className={styles.NavigationItems}>
    <NavigationItem link='/' active>UI/UX</NavigationItem>
    <NavigationItem link='/'>Brandig</NavigationItem>
    <NavigationItem link='/'>Print</NavigationItem>
    <NavigationItem link='/'>3D Desing</NavigationItem>
    <NavigationItem link='/'>Book Desing</NavigationItem>
    <NavigationItem link='/'>Product Desing</NavigationItem>
    </ul>
    );
}

export default navigationItems;