import React from 'react';
import styles from './Header.module.scss';
import israelLogo from '../../../images/icons8-israel-48.png';
import englishLogo from '../../../images/icons8-great-britain-48.png'
import atechLogo from '../../../images/AtechLogoBranding.png';
import NavigationItems from './Navigation//NavigationItems';

const header = (props) => {
    return (
        <header className={styles.Header}>
            <div className={styles.Language}>
                <ul>
                    <li>
                        <img src={israelLogo} />
                    </li>
                    
                    <li>
                        <img src={englishLogo} />
                    </li>
                </ul>
               
            </div>
            <div>
                <NavigationItems />
            </div>
            <div className={styles.Logo}>
                <img src={atechLogo}/>
            </div>
        </header>
    );
}

export default header;