import React from 'react';
import styles from './Main.module.scss';
import whatsappLogo from '../../../images/whatsapp-icon-white.png';
import Icons from '../../../images/drawing-copy.png';
import aIcon from '../../../images/Layer 7.png';
import twitterLogo from '../../../images/icons8-twitter-24.png';
import facebookLogo from '../../../images/icons8-facebook-24.png';
import instagramLogo from '../../../images/icons8-instagram-24.png';
import linkdinLogo from '../../../images/icons8-linkedin-24.png';
import adobeLogo from '../../../images/AdobeIcons.png';

const main = (props) =>{
    return(
        <div className={styles.Main}>
            <div className={styles.LeftSide}>
            <div className={styles.WhatsappLogo}>
              <img src={whatsappLogo} />
            </div>
                <div className={styles.Texts}>
                <h2>A TECH</h2>
                <br></br>
                <h3>Provides You With The Best Custom desing</h3>
                <p>
                you could ever ask for
                <br></br>
                <br></br>
                 Our service is top notch and our pricings are the best available
                 <br></br>
                 <br></br>
                We are the most high end, most affordable studio
                <br></br>
                <br></br>
                that gives you the outmost combination between
                <br></br>
                <br></br>
                quality and price
                </p>
                </div>
                <div className={styles.Icons}>
                    <p>WE ALSO DO A VERIETY OF OTHER THINGS</p>
                    <img src={Icons} />
                </div>
            </div>
            <br></br>
            <div className={styles.RightSide}>
                <div>
                    <ul>
                        <li>
                            <img src={instagramLogo}/>
                        </li>
                        <li>
                            <img src={facebookLogo}/>
                        </li>
                        <li>
                            <img src={twitterLogo}/>
                        </li>
                        <li>
                            <img src={linkdinLogo}/>  
                        </li>
                    </ul>
                </div>
                <div className={styles.Aimage}>
                    <img src={aIcon} />
                </div>

                <div className={styles.RightText}>
                    <p>
                        We use the newest and best in the most executive way
                        <br></br>
                        <br></br>
                        All is done to ensure highest quality of results
                        <br></br>
                        <br></br>
                        
                        We provide you with a key to a successful brand
                        <br></br>
                        <br></br>
                        In the form of digital art
                    </p>
                    <img src={adobeLogo} />
                    <p>
                    We also specialize in UI (and UX) and you can check out
                        <br></br>
                        
                    all of our portfolios <a href="/">here</a>
                    </p>
                </div>
                <div className={styles.ContactUs}>
                <a href='/'>GET IN TOUCH TO GET STARTED</a>
                <form>
                <input type="text" placeholder="Full Name"/>
                <input type="number" placeholder="Number"/>
                <button>submit</button>
                </form>
            </div>
            </div>
            
        </div>
    );
}

export default main;
